import { useEffect, useState } from "react";
import { useParams, useHistory } from "react-router-dom";
import "./styles.css";
import { Col, Row, Button } from "antd";
import "./index.css";

export function Capricorn() {
  const history = useHistory();

  return (
    <div className="fontPage">
      <Row>
        <Row className="fonHeader">
          <Col span={24}>
            <img src="capricorn.png" alt="Capricorn" width="150" />
          </Col>
          <Col span={24}>ราศีมังกร (Capricorn)</Col>
          <Col span={24} className="fonHeader1">
            ผู้ที่เกิดวันที่ 14 ม.ค.- 12 ก.พ.
          </Col>
        </Row>
        <Col span={24}>
          ผู้ที่เกิดราศีมังกร จากการทำนายของนักปราชญ์ นักพยากรณ์ นักคณิตศาสตร์
          นักเศรษฐศาสตร์ นักธรณีวิทยา นักอุตุนิยมวิทยา นักดาราศาสตร์ นักชีววิทยา
          และนักเคมี สรุปได้ว่า พวกคุณมีเลขนำโชค คือ เลข 3
          เลขเดียวกับเบอร์พรรคของเรา อ๊ะ ๆ หยอกเล่น อย่างพึ่งงอน ๆ
          ลองแตะปุ่มข้างล่างดูก่อน อ๊ะ ๆ หยอกเล่น อย่างพึ่งงอน ๆ
          ลองแตะปุ่มข้างล่างดูก่อน
        </Col>
        <Col span={24} className="Space1"></Col>
        <Col span={24} className="center">
          <Button
            type="text"
            onClick={() => history.push("/capricorn1")}
            className="buttonStyle2"
          >
            - แตะเพื่อไปต่อ -
          </Button>
        </Col>
      </Row>
    </div>
  );
}
